
.leftbar {
  background: none repeat scroll 0 0 #FFFFFF;
  border-right: 1px solid #C8C7C7;
  float: left;
  height: auto !important;
  height:800px;
  min-height: 800px;
  padding:0 10px;
  width: 220px;
}
.slidetitle {
  margin-top: 5px; 
  line-height:20px;
  border-bottom:1px dashed #C8C7C7;
}
.slidetitle h2 {
  margin: 0;
  margin-top: 5px;
  text-align: left;
  font-size: 14px;
}
.persontag {
  padding-bottom: 10px;
}
.persontag .avatar {
  float: left;
  width: 70px;
  height: 70px;
  padding: 2px;
  margin-top: 20px;
  background-color: #ececec;
  box-shadow: 0px 0px 1px rgba(0,0,0,0.4);
  border-radius: 35px;
}
.persontag .pavatar {
  width: 70px;
  height: 70px;
  display: inline-block;
  border-radius: 50%; /* relative value */
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  transition: linear 0.25s;
}
.persontag .pepdet {
  float: left;
  padding-left: 25px;
  padding-top: 5px;
  text-align: left;
}
.persontag .pepdet p {
  font-size: 12px;
  color: #000;
}
.persontag .pepdet .pepname {
  color: #000000;
  font-size: 16px;
  padding-bottom: 10px;
  font-family: "Microsoft YaHei";
}
.persontag .clear {
  clear: both;
}






